import Vue from 'vue';
import Main from './main.vue';

// ä½¿ç¨åºç¡ Vue æé å¨ï¼åå»ºä¸ä¸ªâå­ç±»â
let ToastConstructor = Vue.extend(Main);
let instance = null;
let instances = [];
let instanceOnClose = null; // åèª¿

const Toast = function(options) {
  // å¯ä¸æ§
  if(instances.length !== 0) {
    instance.vm.$destroy();
    document.body.removeChild(instance.vm.$el);
  }

  // åè°ä¸closeè½¬æ¢ é¿åéå¤è§¦å
  instanceOnClose = options.onClose;
  options.onClose = function() {
    Toast.close()
  }

  // åå»ºå®ä¾
  instance = new ToastConstructor({
    data: options
  });

  // æè½½å°åç´ 
  instance.vm = instance.$mount();
  document.body.appendChild(instance.vm.$el);
  instance.vm.visible = true;

  instances.push(instance)
  return instance.vm;
};

// ç®ç¥æ¨¡å¼ åªæ¥åmessageåæ°
['success', 'warning', 'loading', 'info'].forEach(type => {
  Toast[type] = options => {
    if(typeof options === 'string') {
      options = {
        message: options
      };
    }
    options.type = type;
    return Toast(options);
  };
});

Toast.close = function() {
  if(instances.length !== 0) {
    if(instanceOnClose) {
      instanceOnClose();
    }
    instance.vm.$destroy();
    document.body.removeChild(instance.vm.$el);
    // reset
    instances.length = 0;
  }
};

export default Toast;
